
import { defineComponent } from 'vue';
import { mapGetters } from 'vuex'
import animePlaceholder from '@/interfaces/animePlaceholder'
import { IAnime } from '@/interfaces/IAnime';
import ReviewCard from '@/components/ReviewCard.vue'

export default defineComponent({
  name: 'AnimeView',
  data() {
    return {
      anime: {} as IAnime,
      notFound: false,
      isLoading: true
    }
  },
  components: { ReviewCard },
  async mounted() {
    this.fetchAnime()
  },
  computed: mapGetters(['allAnimes']),
  methods: {
    async fetchAnime() {
      this.notFound = false
      this.isLoading = true
      this.anime = animePlaceholder
      try {
        const response = await fetch(`https://api.jikan.moe/v4/anime/${this.$route.params.id}/full`)
          .then(res => res.ok ? res.json() : this.notFound = true)
          .then(data => data.data)

        if (response) {
          this.anime = response
          const characters = await fetch(`https://api.jikan.moe/v4/anime/${this.$route.params.id}/characters`)
            .then(res => res.ok ? res.json() : this.notFound = true)
            .then(data => data.data)
          const mainCharacters = characters.filter((character: any) => {
            if (character.role === "Main") return true
            else return false
          })
          if (characters) {
            this.anime.characters = mainCharacters
          }

          const reviews = await fetch(`https://api.jikan.moe/v4/anime/${this.$route.params.id}/reviews`)
            .then(res => res.ok ? res.json() : this.notFound = true)
          if (reviews) {
            this.anime.reviews = reviews
          }
        }
        else {
          this.notFound = true
        }
        this.isLoading = false
      }
      catch (e) {
        console.log(e)
      }
    }
  }
})
